<template>
  <div class="menu2">
    <div class="title" @click="scrollerTo">
      {{ title }}
    </div>
  </div>
</template>
<script>
export default {
  props: {
    title: String,
    //锚点Id
    anchorId: {
      type: String,
      default: "",
    },
  },
  components: {},
  setup(props) {
    function scrollerTo() {
      const anchorDom = document.querySelector(`#${props.anchorId}`);
      if (anchorDom != null) {
        const top = anchorDom.offsetTop - 70;
        const con = document.querySelector(".pageBody .main");
        if (con != null && anchorDom != null) {
          con.scrollTo(0, top);
        }
      }
    }
    return { scrollerTo };
  },
};
</script>
<style lang="scss" scoped>
.menu2 {
  cursor: pointer;
  .title {
    display: inline-block;
    font-size: 1em;
    font-weight: 400;
    margin-left: 25px;
    line-height: 1.5em;
    color: #2c3e50;
    position: relative;
    &::before {
      content: "";
      position: absolute;
      bottom: -1px;
      left: 50%;
      transform: translateX(-50%);
      width: 0%;
      height: 2px;
      background: #3eaf7c;
      transition: 0.3s;
    }
    &:hover::before {
      width: 100%;
    }
  }
}
</style>
